'use client';

import Image from 'next/image';

const gateways = [
  {
    name: 'Mercado Pago',
    methods: ['PIX', 'Boleto'],
    image: '/images/gateways/mp.svg',
  },
  {
    name: 'Asaas',
    methods: ['PIX', 'Boleto'],
    image: '/images/gateways/asaas.png',
  },
  {
    name: 'Cora',
    methods: ['PIX', 'Boleto'],
    image: '/images/gateways/cora.png',
  },
  {
    name: 'EFI Bank',
    methods: ['PIX', 'Boleto'],
    image: '/images/gateways/efi.png',
  },
];

export default function GatewaysSection() {
  return (
    <section
      id="gateways"
      className="py-20 lg:py-32 px-4 sm:px-6 lg:px-8 bg-gradient-to-br from-sky-50 to-cyan-50 dark:from-gray-800 dark:to-gray-900"
    >
      <div className="container mx-auto max-w-7xl">
        {/* Section Header */}
        <div className="text-center mb-16">
          <h2 className="text-3xl sm:text-4xl lg:text-5xl font-bold text-gray-900 dark:text-white mb-4">
            Integrado com os{' '}
            <span className="bg-gradient-to-r from-blue-600 to-cyan-500 bg-clip-text text-transparent">
              principais gateways
            </span>
          </h2>
          <p className="text-lg text-gray-600 dark:text-gray-300 max-w-2xl mx-auto mb-6">
            Escolha o gateway que melhor se adapta ao seu negócio
          </p>
          <div className="inline-flex items-center px-4 py-2 rounded-full bg-blue-100 dark:bg-blue-900/30 border border-blue-200 dark:border-blue-800">
            <span className="text-sm font-semibold text-blue-700 dark:text-blue-300">
              ✓ Suporte a PIX e Boleto em todos os gateways
            </span>
          </div>
        </div>

        {/* Gateways Grid */}
        <div className="grid sm:grid-cols-2 lg:grid-cols-4 gap-6">
          {gateways.map((gateway, index) => (
            <div
              key={index}
              className="group p-6 bg-white dark:bg-gray-800 rounded-xl border-2 border-gray-200 dark:border-gray-700 hover:border-blue-500 dark:hover:border-blue-500 transition-all duration-300 hover:shadow-xl"
            >
              <div className="w-20 h-20 rounded-lg flex items-center justify-center mb-4 mx-auto group-hover:scale-110 transition-transform">
                <Image
                  src={gateway.image}
                  alt={gateway.name}
                  width={80}
                  height={80}
                  className="object-contain"
                />
              </div>
              <h3 className="text-xl font-bold text-gray-900 dark:text-white text-center mb-4">
                {gateway.name}
              </h3>
              <div className="flex flex-wrap gap-2 justify-center">
                {gateway.methods.map((method, methodIndex) => (
                  <span
                    key={methodIndex}
                    className="px-3 py-1 text-xs font-semibold bg-gray-100 dark:bg-gray-700 text-gray-700 dark:text-gray-300 rounded-full"
                  >
                    {method}
                  </span>
                ))}
              </div>
            </div>
          ))}
        </div>

        {/* Additional Info */}
        <div className="mt-12 text-center">
          <p className="text-gray-600 dark:text-gray-300">
            Todos os gateways suportam webhooks para baixa automática de pagamentos
          </p>
        </div>
      </div>
    </section>
  );
}

