'use client';

import Link from 'next/link';
import { APP_NAME } from '@/config/appConfig';

export default function CTASection() {
  return (
    <section className="py-20 lg:py-32 px-4 sm:px-6 lg:px-8 bg-white dark:bg-gray-900">
      <div className="container mx-auto max-w-4xl">
        <div className="text-center bg-gradient-to-br from-blue-50 to-cyan-50 dark:from-gray-800 dark:to-gray-700 rounded-2xl p-12 lg:p-16 border border-gray-200 dark:border-gray-700">
          <h2 className="text-3xl sm:text-4xl lg:text-5xl font-bold text-gray-900 dark:text-white mb-4">
            Pronto para automatizar suas cobranças?
          </h2>
          <p className="text-lg sm:text-xl text-gray-600 dark:text-gray-300 mb-8 max-w-2xl mx-auto">
            Comece seu teste grátis de 7 dias agora e descubra como o {APP_NAME} pode transformar a gestão financeira do seu negócio.
          </p>
          
          <div className="flex flex-col sm:flex-row gap-4 justify-center items-center">
            <Link
              href="/auth/register"
              className="px-10 py-5 bg-gradient-to-r from-blue-600 to-cyan-500 text-white rounded-lg font-bold text-lg hover:from-blue-700 hover:to-cyan-600 transition-all shadow-lg hover:shadow-xl transform hover:-translate-y-0.5 w-full sm:w-auto"
            >
              Criar Conta Grátis
            </Link>
            <Link
              href="/auth/login"
              className="px-10 py-5 bg-white dark:bg-gray-800 text-gray-900 dark:text-white border-2 border-gray-300 dark:border-gray-700 rounded-lg font-semibold text-lg hover:border-blue-600 dark:hover:border-blue-400 transition-all w-full sm:w-auto"
            >
              Já tem conta? Entrar
            </Link>
          </div>

          <p className="mt-6 text-sm text-gray-500 dark:text-gray-400">
            Sem cartão de crédito • Cancelamento a qualquer momento • Suporte completo
          </p>
        </div>
      </div>
    </section>
  );
}

