'use client';

const steps = [
  {
    number: '1',
    title: 'Cadastre-se',
    description: 'Crie sua conta em menos de 2 minutos. Teste grátis por 7 dias, sem cartão de crédito.',
  },
  {
    number: '2',
    title: 'Configure seu gateway',
    description: 'Conecte seu gateway de pagamento preferido (Mercado Pago, Asaas, Cora ou EFI Pay).',
  },
  {
    number: '3',
    title: 'Cadastre clientes e crie cobranças',
    description: 'Importe seus clientes e configure cobranças recorrentes de forma simples e rápida.',
  },
  {
    number: '4',
    title: 'Automatize envios via WhatsApp',
    description: 'Configure mensagens automáticas que serão enviadas no horário programado.',
  },
  {
    number: '5',
    title: 'Receba pagamentos automaticamente',
    description: 'O sistema reconhece pagamentos via webhook e faz a baixa automática instantaneamente.',
  },
];

export default function HowItWorksSection() {
  return (
    <section className="py-20 lg:py-32 px-4 sm:px-6 lg:px-8 bg-gradient-to-br from-sky-50 via-white to-cyan-50 dark:from-gray-800 dark:via-gray-900 dark:to-gray-800">
      <div className="container mx-auto max-w-7xl">
        {/* Section Header */}
        <div className="text-center mb-16">
          <h2 className="text-3xl sm:text-4xl lg:text-5xl font-bold text-gray-900 dark:text-white mb-4">
            Como{' '}
            <span className="bg-gradient-to-r from-blue-600 to-cyan-500 bg-clip-text text-transparent">
              funciona
            </span>
          </h2>
          <p className="text-lg text-gray-600 dark:text-gray-300 max-w-2xl mx-auto">
            Em 5 passos simples, você está pronto para automatizar suas cobranças
          </p>
        </div>

        {/* Steps */}
        <div className="relative">
          {/* Connection Line (Desktop) */}
          <div className="hidden lg:block absolute top-24 left-0 right-0 h-0.5 bg-gradient-to-r from-blue-200 via-cyan-200 to-blue-200 dark:from-blue-800 dark:via-cyan-800 dark:to-blue-800"></div>

          <div className="grid sm:grid-cols-2 lg:grid-cols-5 gap-8">
            {steps.map((step, index) => (
              <div key={index} className="relative">
                {/* Step Card */}
                <div className="bg-white dark:bg-gray-800 rounded-xl p-6 border-2 border-gray-200 dark:border-gray-700 hover:border-sky-500 dark:hover:border-sky-500 transition-all duration-300 hover:shadow-xl text-center">
                  {/* Number Badge */}
                  <div className="w-16 h-16 bg-gradient-to-br from-blue-600 to-cyan-500 rounded-full flex items-center justify-center mx-auto mb-4 text-white text-2xl font-bold">
                    {step.number}
                  </div>
                  <h3 className="text-xl font-bold text-gray-900 dark:text-white mb-3">
                    {step.title}
                  </h3>
                  <p className="text-gray-600 dark:text-gray-300 leading-relaxed">
                    {step.description}
                  </p>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* CTA */}
        <div className="mt-12 text-center">
          <p className="text-gray-600 dark:text-gray-300 mb-6">
            Pronto para começar? O processo é simples e rápido!
          </p>
          <a
            href="/auth/register"
            className="inline-block px-8 py-4 bg-gradient-to-r from-blue-600 to-cyan-500 text-white rounded-lg font-semibold text-lg hover:from-blue-700 hover:to-cyan-600 transition-all shadow-lg hover:shadow-xl"
          >
            Começar Agora
          </a>
        </div>
      </div>
    </section>
  );
}

