'use client';

import Link from 'next/link';
import { APP_NAME } from '@/config/appConfig';

export default function TrialSection() {
  return (
    <section className="py-20 lg:py-32 px-4 sm:px-6 lg:px-8 bg-gradient-to-br from-blue-600 via-cyan-500 to-sky-700 dark:from-blue-800 dark:via-cyan-700 dark:to-blue-900">
      <div className="container mx-auto max-w-4xl">
        <div className="text-center text-white">
          {/* Badge */}
          <div className="inline-flex items-center px-6 py-3 rounded-full bg-white/20 backdrop-blur-sm border border-white/30 mb-8">
            <span className="text-2xl font-bold mr-2">7 DIAS GRÁTIS</span>
            <svg className="w-6 h-6" fill="currentColor" viewBox="0 0 20 20">
              <path
                fillRule="evenodd"
                d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z"
                clipRule="evenodd"
              />
            </svg>
          </div>

          {/* Headline */}
          <h2 className="text-3xl sm:text-4xl lg:text-5xl font-bold mb-6">
            Teste grátis por 7 dias
          </h2>

          {/* Description */}
          <p className="text-lg sm:text-xl text-blue-100 mb-8 max-w-2xl mx-auto leading-relaxed">
            Experimente todas as funcionalidades do {APP_NAME} sem compromisso. Sem cartão de crédito, sem pegadinhas.
            Cancele quando quiser, sem custos adicionais.
          </p>

          {/* Features List */}
          <div className="grid sm:grid-cols-2 gap-4 mb-10 text-left max-w-2xl mx-auto">
            <div className="flex items-center gap-3">
              <svg className="w-6 h-6 text-green-300 flex-shrink-0" fill="currentColor" viewBox="0 0 20 20">
                <path
                  fillRule="evenodd"
                  d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z"
                  clipRule="evenodd"
                />
              </svg>
              <span className="text-blue-100">Acesso completo a todas as funcionalidades</span>
            </div>
            <div className="flex items-center gap-3">
              <svg className="w-6 h-6 text-green-300 flex-shrink-0" fill="currentColor" viewBox="0 0 20 20">
                <path
                  fillRule="evenodd"
                  d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z"
                  clipRule="evenodd"
                />
              </svg>
              <span className="text-blue-100">Sem necessidade de cartão de crédito</span>
            </div>
            <div className="flex items-center gap-3">
              <svg className="w-6 h-6 text-green-300 flex-shrink-0" fill="currentColor" viewBox="0 0 20 20">
                <path
                  fillRule="evenodd"
                  d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z"
                  clipRule="evenodd"
                />
              </svg>
              <span className="text-blue-100">Suporte completo durante o teste</span>
            </div>
            <div className="flex items-center gap-3">
              <svg className="w-6 h-6 text-green-300 flex-shrink-0" fill="currentColor" viewBox="0 0 20 20">
                <path
                  fillRule="evenodd"
                  d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z"
                  clipRule="evenodd"
                />
              </svg>
              <span className="text-blue-100">Cancelamento fácil e sem custos</span>
            </div>
          </div>

          {/* CTA */}
          <Link
            href="/auth/register"
            className="inline-block px-10 py-5 bg-white text-blue-600 rounded-lg font-bold text-lg hover:bg-gray-100 transition-all shadow-2xl hover:shadow-3xl transform hover:-translate-y-1"
          >
            Começar Agora - Sem Cartão de Crédito
          </Link>

          {/* Guarantee */}
          <p className="mt-6 text-sm text-blue-200">
            ✓ Garantia de cancelamento sem custos
          </p>
        </div>
      </div>
    </section>
  );
}

