'use client';

import { useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { getCookie } from '@/utils/cookies';
import RedirectLoading from './RedirectLoading';

export default function ClientRedirect() {
  const router = useRouter();

  useEffect(() => {
    const handleRedirect = () => {
      const token = getCookie('token');
      const userStr = getCookie('user');

      // Se usuário está logado, redirecionar para dashboard apropriado
      if (token && userStr) {
        try {
          const userData = JSON.parse(userStr);
          const userType = userData.tipo;

          if (userType === 'SUPERADMIN') {
            router.replace('/dashboard/admin');
          } else if (userType === 'EMPRESA') {
            router.replace('/dashboard/empresa');
          } else if (userType === 'CLIENTE') {
            router.replace('/dashboard/cliente');
          } else {
            router.replace('/dashboard/empresa'); // fallback padrão
          }
        } catch (error) {
          console.error('Erro ao parsear dados do usuário:', error);
          // Se não conseguir parsear o user, redirecionar para login
          router.replace('/auth/login');
        }
      } else {
        // Se não está logado, redirecionar para login
        router.replace('/auth/login');
      }
    };

    // Executar após um pequeno delay para evitar problemas de hydration
    const timeoutId = setTimeout(handleRedirect, 100);

    return () => clearTimeout(timeoutId);
  }, [router]);

  return <RedirectLoading />;
}
